.features-item {
  box-sizing: border-box;
  width: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  list-style: none;
  position: relative;
  padding-top: 111px;

  @media (max-width: 830px) {
    width: 249px;
  }

  @media (max-width: 510px) {
    width: auto;
    padding-left: 80px;
    padding-top: 2px;
  }

  &::after {
    background-color: $color_apple;
    width: 80px;
    height: 80px;
    content: "";
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;

    @media (max-width: 510px) {
      width: 60px;
      height: 60px;
    }
  }

  &::before {
    counter-increment: section;
    content: counter(section);
    position: absolute;
    right: 0;
    top: -40px;
    z-index: 0;
    font-weight: normal;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;

    @media (max-width: 510px) {
      content: "";
    }
  }
}

.features-item__text {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  position: relative;
  z-index: 1;
  color: $color_tundora;
  margin-bottom: 43px;

  @media (max-width: 480px) {
    margin-bottom: 0;
    font-size: 14px;
    line-height: 18px;
  }

  @media (max-width: 510px) {
    br {
      display: none;
    }
  }
}

.features-item--useful {
  @media (min-width: $width-desktop+px) {
    &::before {
      right: 15px;
    }
  }

  @media (max-width: $width-tablet+px) {
    &::before {
      right: 20px;
    }
  }

  @media (max-width: $width-tablet+px) and (min-width: $width-desktop+px) {
    width: 230px;
  }

  &::after {
    background-image: url("../img/advantage-leaf@1x.png");
    background-size: 50px;
    background-position: center;
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/advantage-leaf@2x.png");
    }
  }
}

.features-item--easy {
  @media (max-width: $width-tablet+px) and (min-width: $width-desktop+px) {
    width: 246px;
    margin-left: 8px;
  }

  &::after {
    background-image: url("../img/advantage-powder@1x.png");
    background-position: center;
    background-size: 50px;
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/advantage-powder@2x.png");
    }
  }

  @media (max-width: $width-tablet+px) {
    margin-left: 9px;
  }

  @media (max-width: 510px) {
    margin-left: 0;
  }
}

.features-item--cheap {
  &::after {
    background-image: url("../img/advantage-dining@1x.png");
    background-position: center;
    background-size: 50px;
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/advantage-dining@2x.png");
    }
  }

  @media (min-width: $width-desktop+px) {
    &::before {
      right: -2px;
    }
  }
}

.features-item--result {
  @media (max-width: $width-tablet+px) and (min-width: $width-desktop+px) {
    margin-left: 8px;
  }

  &::after {
    background-image: url("../img/advantage-clock@1x.png");
    background-position: center;
    background-size: 50px;
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/advantage-clock@2x.png");
    }
  }

  @media (min-width: $width-desktop+px) {
    &::before {
      right: -4px;
    }
  }

  @media (max-width: $width-tablet+px) {
    margin-left: 9px;
  }

  @media (max-width: 510px) {
    margin-left: 0;
  }
}
