.gift-block {
  box-sizing: border-box;
  flex: 0 1 245px;
  font-family: "Oswald", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("../img/gift-photo-desktop@1x.jpg") no-repeat;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/gift-photo-desktop@2x.jpg");
  }

  @media (max-width: $width-tablet+px) {
    background: url("../img/gift-photo-tablet@1x.jpg") no-repeat;
    background-size: cover;
    flex: 0 1 200px;
    flex-direction: row;
    align-items: center;
    height: 200px;
    justify-content: space-between;
    padding: 0 80px/1440px * 100vw 0;
    margin: 0;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }
  }

  @media (max-width: $width-mobile+px) {
    background: url("../img/gift-photo-mobile@1x.jpg") no-repeat;
    background-size: cover;
    flex: 0 1 289px;
    height: 289px;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    margin: 0;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/gift-photo-mobile@2x.jpg");
    }
  }
}

.gift-block__img {
  width: 57px;
  height: 80px;
  margin-top: 12px;
  margin-bottom: 55px;

  @media (max-width: $width-tablet+px) {
    height: 62px;
    width: 72px;
    margin: 0 0 9px 39px;
  }

  @media (max-width: $width-mobile+px) {
    margin: 64px 0 62px;
  }
}

.gift-block__text {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: $color_white;
  text-align: center;
  width: 161px;

  @media (max-width: $width-tablet+px) {
    text-align: left;
    margin: 4px 81px 0 0;
  }

  @media (max-width: $width-mobile+px) {
    margin: 0;
    text-align: center;
  }
}
